.vip-item {
  border-bottom: 1px solid #eeeeee;
  position: relative;
  padding: 0.16rem 0;
}
.vip-item .tit-tips {
  position: relative;
  margin-bottom: 0.1rem;
}
.vip-item .tit-tips .tit-tips-l {
  display: inline-block;
  width: 0.04rem;
  height: 0.18rem;
  background-color: #fff3d8;
  margin-right: 0.04rem;
  position: absolute;
  left: 0;
  bottom: 0;
}
.vip-item .tit-tips .tit-tips-r {
  padding: 0 0.08rem;
  display: inline-block;
  background-color: #fff3d8;
  color: #c18b24;
  font-size: 0.1rem;
  line-height: 0.18rem;
  margin-left: 0.1rem;
}
.vip-item .vip-item-l {
  width: 2.26rem;
}
.vip-item .vip-item-l h2 {
  color: #333333;
  font-size: 0.16rem;
  margin-bottom: 0.07rem;
}
.vip-item .vip-item-l > div {
  line-height: 0.17rem;
  color: #999999;
  font-size: 0.12rem;
  text-align: left;
}
.vip-item .vip-item-l .time {
  margin-bottom: 0.12rem;
}
.vip-item .vip-item-l .bottom > p {
  width: 50%;
  float: left;
}
.vip-item .vip-item-r img {
  width: 1.09rem;
  height: 0.76rem;
  background: #ddd;
}
