.course-empty {
  padding: 0.6rem 0;
  text-align: center;
}
.course-empty > img {
  width: 1.5rem;
  margin-bottom: 0.4rem;
}
.course-empty .empty-i {
  text-align: center;
}
.course-empty p {
  color: #333333;
  font-size: 16px;
  text-align: center;
  width: 1.6rem;
  margin: 0 auto;
  margin-bottom: 0.2rem;
}
.course-empty .empty-btn {
  width: 2.8rem;
  height: 0.4rem;
  border-radius: 0.04rem;
  background-color: #dba131;
  color: #ffffff;
  font-size: 0.16rem;
  line-height: 0.4rem;
  margin: 0 auto;
}
.course-empty .empty-btn.vip {
  width: 1.7rem;
}
.textarea {
  display: inline-block;
  width: 2.06rem;
  height: 1.1rem;
  border-radius: 0.04rem;
  background-color: #ffffff;
  border: 0.005rem solid #dddddd;
  padding: 0.1rem 0.11rem;
}
