.top-title {
  width: 3.75rem;
  height: 0.36rem;
  background-color: #fff9ec;
  line-height: 0.36rem;
  color: #e9b551;
  font-size: 0.14rem;
  text-align: center;
}
.top-title span {
  font-size: 0.2rem;
  display: inline-block;
  width: 0.3rem;
}
.search-top {
  width: 3.75rem;
  background: #fff;
  position: fixed;
  left: 0;
  top: 0;
  z-index: 999;
}
.all-form {
  width: 100%;
  background: #fff;
  z-index: 10;
}
.all-form .form-item {
  width: 3.45rem;
  padding: 0.1rem 0.15rem;
}
.all-form .form-item .search-tit {
  width: 2.6rem;
  height: 0.3rem;
  background-color: #f2f2f2;
  border: none;
  padding-left: 0.32rem;
  font-size: 0.14rem;
  background-image: url(data:image/png;base64,);
  background-repeat: no-repeat;
  background-position: 0.1rem center;
  background-size: 0.2rem;
}
.all-form .form-item .search-submit {
  color: #333333;
  font-size: 0.16rem;
  text-align: center;
  line-height: 0.3rem;
  border: none;
  background: none;
  margin-left: 0.15rem;
}
.all-form .hot-form {
  padding: 0.15rem 0.25rem;
}
.all-form .hot-form h3 {
  line-height: 0.25rem;
  color: #333333;
  font-size: 0.18rem;
  margin-bottom: 0.15rem;
}
.all-form .hot-form .hot-form-item span {
  float: left;
  padding: 0.06rem 0.12rem;
  background-color: #f5f5f5;
  margin-right: 0.12rem;
  margin-bottom: 0.12rem;
}
.all-form .history-form {
  padding: 0.15rem 0.25rem;
}
.all-form .history-form h3 {
  line-height: 0.25rem;
  color: #333333;
  font-size: 0.18rem;
  margin-bottom: 0.15rem;
}
.all-form .history-form .history-form-item span {
  float: left;
  padding: 0.06rem 0.12rem;
  background-color: #f5f5f5;
  margin-right: 0.12rem;
  margin-bottom: 0.12rem;
}
.nav-bar {
  display: flex;
  flex-direction: row;
  justify-content: center;
  flex-wrap: wrap;
  width: 3.45rem;
  padding: 0.15rem 0.15rem 0;
}
.nav-bar .nav-bar-i {
  flex-grow: 0;
  flex-shrink: 0;
  width: 25%;
  text-align: center;
  margin-bottom: 0.15rem;
}
.nav-bar .nav-bar-i img {
  width: 0.4rem;
  height: 0.4rem;
}
.nav-bar .nav-bar-i p {
  color: #333333;
  font-size: 0.11rem;
}
.filter-c {
  width: 100%;
  height: 0.45rem;
  background-color: #ffffff;
  box-shadow: 0px 1px 0px 0px #eee;
}
.filter-c .filter-header {
  border-bottom: 1px solid #eee;
  text-align: center;
}
.filter-c .filter-header .am-flexbox-item {
  color: #333333;
  font-size: 0.16rem;
  text-align: center;
  line-height: 0.3rem;
  margin: 0.075rem 0;
  border-right: 0.01rem solid #eeeeee;
  position: relative;
}
.filter-c .filter-header .am-flexbox-item .am-badge {
  display: inline-block;
  line-height: 1;
  vertical-align: middle;
  position: absolute;
  right: 0.25rem;
  top: 0;
}
.filter-c .filter-header .am-flexbox-item .am-badge .am-badge-text {
  min-width: 0.05rem;
  height: 0.12rem;
  line-height: 0.12rem;
  font-size: 0.1rem;
  background-color: #fff3d8;
  color: #DBA131;
}
.filter-c .filter-header .am-flexbox-item:last-child {
  border-right: 0;
}
.filter-c .filter-header .am-flexbox-item img {
  width: 0.08rem;
  vertical-align: middle;
}
.filter-c .filter-box {
  height: 100vh;
  position: relative;
}
.filter-c .filter-mask {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -1;
  background: rgba(0, 0, 0, 0.5);
}
.filter-c .filter-content {
  background: #fff;
  padding: 0.16rem;
}
.filter-c .filter-content-bd {
  height: 3.5rem;
  overflow: auto;
}
.filter-c .city-right {
  position: absolute;
  right: 0.05rem;
  top: 0.1rem;
  width: 0.1rem;
}
.filter-c .city-right a {
  display: block;
  width: 0.09rem;
  font-size: 0.09rem;
  font-weight: 400;
  color: #999999;
  line-height: 0.13rem;
  text-align: center;
}
.filter-c .filter-provice {
  padding: 0.08px 0;
}
.filter-c .filter-provice-tt {
  line-height: 0.2rem;
  color: #999;
  font-size: 0.14rem;
}
.filter-c .filter-provice-bd {
  padding: 0.08rem 0;
}
.filter-c .filter-provice-bd .am-tag {
  min-width: 0.72rem;
  height: 0.34rem;
  line-height: 0.34rem;
  margin-right: 0.18rem;
  margin-bottom: 0.08rem;
}
.filter-c .filter-provice-bd .am-tag:nth-child(4n) {
  margin-right: 0;
}
.filter-c .filter-provice-bd .am-tag-active {
  background-color: #DBA131;
  color: #fff;
}
.filter-c .filter-series .am-tag {
  width: 1.65rem;
  height: 0.4rem;
  line-height: 0.4rem;
  margin-right: 0.1rem;
  margin-bottom: 0.08rem;
}
.filter-c .filter-series .am-tag .am-tag-text {
  height: 100%;
  padding-right: 0.05rem;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}
.filter-c .filter-series .am-tag:nth-child(2n) {
  margin-right: 0;
}
.filter-c .filter-series .am-tag-active {
  background-color: #DBA131;
  color: #fff;
}
.filter-c .filter-card .am-tag {
  height: auto;
  padding: 0.02rem 0.02rem;
  margin-right: 0.05rem;
}
.filter-c .filter-card .am-tag:nth-of-type(even) {
  margin-right: 0;
}
.filter-c .filter-card .card-item {
  position: relative;
  width: 1.64rem;
  height: 0.72rem;
  border-radius: 0.05rem;
  background-color: #FFFFFF;
}
.filter-c .filter-card .card-item > .card {
  width: 100%;
}
.filter-c .filter-card .card-item > .active {
  width: 0.3rem;
  position: absolute;
  left: 0;
  top: 0;
  display: none;
}
.filter-c .filter-card .card-item div {
  position: absolute;
}
.filter-c .filter-card .card-item div span {
  font-size: 0.2rem;
  font-weight: 500;
}
.filter-c .filter-card .card-item > .title {
  color: #ffffff;
  font-size: 0.07rem;
  left: 0.14rem;
  top: 0.1rem;
}
.filter-c .filter-card .card-item > .con {
  color: #ffffff;
  font-size: 0.08rem;
  left: 0.14rem;
  top: 0.28rem;
}
.filter-c .filter-card .card-item > .number {
  color: #ffffff;
  font-size: 0.09rem;
  left: 0.14rem;
  bottom: 0.04rem;
}
.filter-c .filter-card .card-item > .type {
  font-size: 0.08rem;
  color: #ffffff;
  right: 0.1rem;
  bottom: 0.04rem;
}
.filter-c .filter-card .am-tag-active .active {
  display: block;
}
.company-search {
  width: 2.7rem;
  height: 3.5rem;
  border-radius: 0.05rem;
  background-color: #ffffff;
  box-shadow: 0rem 0.08rem 0.16rem 0rem rgba(0, 0, 0, 0.1);
  position: fixed;
  left: 0.5rem;
  top: 0.6rem;
  background-color: #fff;
  z-index: 11;
  overflow: scroll;
}
.company-search .search-i {
  padding: 16px;
  border-bottom: 1px solid #EEEEEE;
}
.company-search .search-i > p:first-child {
  color: #333333;
  font-size: 0.15rem;
}
.company-search .search-i > p:last-child {
  color: #999999;
  font-size: 0.1rem;
}
